import React, { Component } from "react";
import ReactDom from "react-dom";
import ImageOne from "./flag.jpg";
import { Link, BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import "./style.css";

class Login extends Component {
  state = {
    credentials: {
      username: "",
      password: "",
      person: {
        user_panchayath: "",
      },
    },
    siginError: false,
    mytoken: "",
    hidden: true,
  };

  login = (event) => {
    console.log(this.state.credentials);
    fetch("http://127.0.0.1:8000/auth/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(this.state.credentials),
    })
      .then((data) => {
        console.log(data);
        if (data.status === 200) {
          this.props.setfetchData(true);
        }

        return data.json();
      })
      .then((data) => {
        if (data.token === undefined) {
          this.setState({ siginError: true });
        } else {
          this.setState({ siginError: false });
        }
        this.setState({ mytoken: data.token });
        this.props.userLogin(data.token);

        this.props.loginUsername(this.state.credentials.username);
        this.props.userPanchayath(
          this.state.credentials.person.user_panchayath
        );
      })
      .catch((err) => this.setState({ siginError: true }));
  };

  inputChanged = (event) => {
    const cred = this.state.credentials;
    if (event.target.name === "user_panchayath") {
      cred.person.user_panchayath = event.target.value;
    } else {
      const eventName = event.target.name;
      cred[eventName] = event.target.value;
    }
    this.setState({ credentials: cred });
  };

  eye = () => {
    this.setState({ hidden: !this.state.hidden });
  };

  render() {
    if (this.state.mytoken) {
      return <Redirect to="/home" />;
    }

    let invalid = <p className="invalid-id">&nbsp;</p>;

    if (this.state.siginError) {
      invalid = <p className="invalid-id">**invalid user name or passowrd</p>;
    }

    return (
      <div className="container">
        <div className="imgcontainer">
          <img className="imgOne" src={ImageOne}></img>
        </div>
        <div className="login-container">
          <h1>Log in</h1>
          <h6>Login to IMIT messenger</h6>
          {invalid}
          <div className="login-form">
            <input
              className="login-form-input"
              id="txt-input"
              type="text"
              placeholder="Email address"
              name="username"
              value={this.state.credentials.username}
              onChange={this.inputChanged}
              required
            ></input>
            <br></br>
            <select
              id="select-input"
              className="form-select"
              name="user_panchayath"
              value={this.state.credentials.person.user_panchayath}
              onChange={this.inputChanged}
            >
              <option value="" disabled defaultValue>
                Select Panchayath
              </option>
              <option value="panch1">Panchayath 1</option>
              <option value="panch2">Panchayath 2</option>
              <option value="panch3">Panchayath 3</option>
              <option value="panch4">Panchayath 4</option>
            </select>
            <input
              className="login-form-input"
              type={this.state.hidden ? "password" : "text"}
              placeholder="Password"
              id="pwd"
              name="password"
              value={this.state.credentials.password}
              onChange={this.inputChanged}
              required
            ></input>
            <span>
              <i
                class={this.state.hidden ? "fa fa-eye" : "fa fa-eye-slash"}
                aria-hidden="true"
                type="button"
                id={this.state.hidden ? "eye" : "eye-red"}
                onClick={this.eye}
              ></i>
            </span>
            <br></br>
            <input
              className="form-checkbox"
              type="checkbox"
              id="rememberpwd"
            ></input>
            <label className="form-checkbox-label" htmlFor="rememberpwd">
              Remember password
            </label>
            <br></br>
            <button className="log-in btn-login" onClick={this.login}>
              SIGN IN{" "}
            </button>
            <a href="#">Forgot password?</a>
            <Link to="/register" className="link-register">
              Register
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
