import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import Login from "./components/login/login";
import Register from "./components/register/register";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import { useCookies, CookiesProvider } from "react-cookie";
import { useHistory } from "react-router-dom";
import Users from "./components/users/users";

function LoginUser() {
  const [token, setToken] = useCookies(["mytoken"]);
  const [panchayath, setPanchayath] = useCookies(["panchayath"]);
  const [userName, setUserName] = useCookies(["username"]);

  const [initfetchData, setinitfetchData] = useCookies(["fetchdata"]);

  const setfetchData = (fetchStatus) => {
    setinitfetchData("fetchdata", fetchStatus);
  };

  let history = useHistory();
  console.log(token["mytoken"]);

  const userLogin = (token) => {
    setToken("mytoken", token);
  };

  const userPanchayath = (panchayath) => {
    setPanchayath("panchayath", panchayath);
  };

  useEffect(() => {
    if (token["mytoken"] != "undefined" && token["mytoken"]) {
      // history.push("/home");
    }
  }, [token]);

  const loginUsername = (userName) => {
    setUserName("username", userName);
  };

  return (
    <div>
      <Login
        userLogin={userLogin}
        userPanchayath={userPanchayath}
        loginUsername={loginUsername}
        setfetchData={setfetchData}
        //stopFetchData={stopFetchData}
      />
    </div>
  );
}

function UserHome() {
  const [token, removeToken] = useCookies(["mytoken"]);
  const [panchayath, removePanchayath] = useCookies(["panchayath"]);
  const [userName, removeUserName] = useCookies(["username"]);
  const [initfetchData, setinitfetchData, removeinitfetchData] = useCookies([
    "fetchdata",
  ]);

  let history = useHistory();
  const logoutBtn = () => {
    removeToken(["mytoken"]);
    removePanchayath(["panchayath"]);
    removeUserName(["username"]);
    history.push("");
  };

  const logIn = () => {
    history.push("");
  };

  const stopfetch = () => {
    removeinitfetchData(["fetchdata"]);
  };

  return (
    <div>
      <Users
        token={token["mytoken"]}
        userpanchayath={panchayath["panchayath"]}
        activeUsername={userName["username"]}
        fetchData={initfetchData["fetchdata"]}
        logOutBtn={logoutBtn}
        logIn={logIn}
        stopfetch={stopfetch}
      ></Users>
    </div>
  );
}

function Router() {
  const [token] = useCookies(["mytoken"]);

  let setRegisterLink = false;

  let registerMainLink = "/register";

  return (
    <CookiesProvider>
      <BrowserRouter>
        <Switch>
          <Route path={registerMainLink} component={Register} />

          <Route path="/home" component={UserHome} />

          <Route exact path="" component={LoginUser} />
        </Switch>
      </BrowserRouter>
    </CookiesProvider>
  );
}

ReactDOM.render(
  <React.StrictMode>
    <Router />
  </React.StrictMode>,
  document.getElementById("root")
);

