import React, { Component, useEffect } from "react";
import ReactDom from "react-dom";
import { Link, BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import "./style.css";

class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      panchayath: {
        id: "",
        panchayath_district: " ",
        panchayath_name: " ",
        panchayath_population: " ",
        panchayath_state: " ",
        user_panchayath: " ",
      },
      person: {
        user_username: "",
        user_wanumber: "",
        user_panchayath: "",
        user_district: "",
        user_profile_pic: "",
      },
      token: this.props.token,
      userpanchayath: this.props.userpanchayath,
      rendered: false,
      activeUsername: this.props.activeUsername,
      editDataStatus: false,
    };
  }
  // state = {
  //   panchayath: {
  //     id: " ",
  //     panchayath_district: " ",
  //     panchayath_name: " ",
  //     panchayath_population: " ",
  //     panchayath_state: " ",
  //     user_panchayath: " ",
  //   },
  //   token: this.props.token,
  //   userpanchayath: this.props.userpanchayath,
  //   rendered: false,
  // };
  loadPanchayath = () => {
    fetch("http://127.0.0.1:8000/api/panchayath/", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${this.state.token}`,
      },
      body: JSON.stringify(this.state.credentials),
    })
      .then((data) => data.json())
      .then((data) =>
        this.setState({
          panchayath: data.find(
            (data) => data.user_panchayath === this.state.userpanchayath
          ),
        })
      )
      .catch((err) => console.error(err));
  };

  inputChanged = (event) => {
    const panch = this.state.panchayath;
    panch[event.target.name] = event.target.value;
    this.setState({ panchayath: panch });
  };

  componentDidMount() {
    fetch("http://127.0.0.1:8000/api/panchayath/", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${this.state.token}`,
      },
      body: JSON.stringify(this.state.credentials),
    })
      .then((data) => data.json())
      .then((data) =>
        this.setState({
          panchayath: data.find(
            (data) => data.user_panchayath === this.state.userpanchayath
          ),
        })
      )
      .catch((err) => console.error(err));

    fetch("http://127.0.0.1:8000/api/users/", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(this.state.credentials),
    })
      .then((data) => data.json())
      .then((data) =>
        this.setState({
          person: data.find((data) => data.email === this.state.activeUsername)
            .person,
        })
      )
      .catch((err) => console.error(err));
  }

  editDatabtn = () => {
    this.setState({ editDataStatus: true });
  };

  updateDatabtn = () => {
    fetch(`http://127.0.0.1:8000/api/panchayath/${this.state.panchayath.id}/`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${this.state.token}`,
      },
      body: JSON.stringify(this.state.panchayath),
    }).then((resp) => {
      this.setState({ editDataStatus: false });
      console.log(resp);
      return resp.json();
    });
  };

  render() {
    if (this.props.fetchData) {
      //this.props.stopFetchData();
      console.log(this.props.fetchData);
      console.log(this.state.person);
      window.location.reload();
      this.props.stopfetch();
    } else {
      console.log(this.props.fetchData);
    }

    return (
      <div className="container">
        <div className="nav-container">
          <h1>
            Hi ,{" "}
            {this.state.token !== "undefined"
              ? this.state.person?.user_username
              : "user"}
          </h1>
          {this.state.token !== "undefined" ? (
            <button onClick={this.props.logOutBtn} className="btn-logout">
              Log out
            </button>
          ) : (
            <button onClick={this.props.logIn} className="btn-logout">
              Log in
            </button>
          )}{" "}
        </div>
        <div className="row">
          <div className="column sidebar">
            <img
              src="https://cdn.business2community.com/wp-content/uploads/2017/08/blank-profile-picture-973460_640.png"
              alt="profile"
            ></img>
            <h2> My Details</h2>
            <hr></hr>
            <h5 className="details">
              Username : {this.state.person?.user_username}
            </h5>
            <h5 className="details">Email : {this.state.activeUsername}</h5>
            <h5 className="details">
              Mobile : {this.state.person?.user_wanumber}
            </h5>
          </div>
          <div className="users-container column">
            {this.state.editDataStatus ? (
              <div className="panchayath-container">
                <div className="">
                  <label htmlFor="a">Panchayath : </label>
                  <input
                    className="edit-form-input"
                    id="a"
                    type="text"
                    placeholder="Panchayath"
                    name="panchayath_name"
                    value={this.state.panchayath?.panchayath_name}
                    onChange={this.inputChanged}
                  ></input>
                  <br></br>
                  <label htmlFor="b">District : </label>
                  <input
                    className="edit-form-input"
                    id="b"
                    type="text"
                    placeholder="District"
                    name="panchayath_district"
                    value={this.state.panchayath?.panchayath_district}
                    onChange={this.inputChanged}
                  ></input>
                  <br></br>

                  <label htmlFor="c">Population : </label>
                  <input
                    className="edit-form-input"
                    id="c"
                    type="text"
                    placeholder="User Name"
                    placeholder="Population"
                    name="panchayath_population"
                    value={this.state.panchayath?.panchayath_population}
                    onChange={this.inputChanged}
                  ></input>
                  <br></br>

                  <label htmlFor="d" id="edit-label">
                    State :{" "}
                  </label>
                  <input
                    className="edit-form-input"
                    id="d"
                    type="text"
                    placeholder="User Name"
                    placeholder="State"
                    name="panchayath_state"
                    value={this.state.panchayath?.panchayath_state}
                    onChange={this.inputChanged}
                  ></input>
                </div>

                <button
                  className="btn-update"
                  onClick={
                    this.state.panchayath?.id ? this.updateDatabtn : null
                  }
                >
                  Update
                </button>
              </div>
            ) : (
              <div className="panchayath-container">
                <h4 key="1">
                  Panchayath :{this.state.panchayath?.panchayath_name}
                </h4>
                <h4 key="2">
                  District : {this.state.panchayath?.panchayath_district}
                </h4>
                <h4 key="3">
                  Population : {this.state.panchayath?.panchayath_population}
                </h4>
                <h4 key="4">
                  State : {this.state.panchayath?.panchayath_state}
                </h4>
                <button className="btn-edit" onClick={this.editDatabtn}>
                  Edit
                </button>
              </div>
            )}
          </div>
        </div>
        <div className="nav-container"></div>
      </div>
    );
  }
}

export default Users;
