import React, { Component } from "react";
import ReactDom from "react-dom";
import ImageOne from "./flag.jpg";
import ImageTwo from "./Green-Check.png";
import { Link, BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import "./style.css";

class Register extends Component {
  state = {
    credentials: {
      email: "",
      password: "",
      mobile_no: "",
      person: {
        user_username: "",
        user_wanumber: "",
        user_panchayath: "",
        user_district: "",
      },
    },
    confirm_password: "",
    registerSucess: false,
    mailInvalid: false,
    allusers: [
      {
        id: "na",
        email: "na",
        mobile_no: "na",
        person: {
          user_username: "na",
          user_wanumber: "na",
          user_panchayath: "na",
          user_district: "na",
        },
      },
    ],
  };

  register = (event) => {
    if (this.state.confirm_password === this.state.credentials.password) {
      fetch("http://127.0.0.1:8000/api/users/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(this.state.credentials),
      })
        .then((data) => data.json())
        .then((data) => {
          console.log(data);
          if (data.email == ["Enter a valid email address."]) {
            this.setState({ mailinValid: true });
          }
          if (data.id) {
            this.setState({
              credentials: {
                email: "",
                password: "",
                mobile_no: "",
                person: {
                  user_username: "",
                  user_wanumber: "",
                  user_panchayath: "",
                  user_district: "",
                },
              },
              confirm_password: "",
              registerSucess: true,
              mailInvalid: false,
            });
          }
        })
        .catch((err) => console.error(err));
    }
  };

  componentDidMount() {
    fetch("http://127.0.0.1:8000/api/users/", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      //body: JSON.stringify(this.state.credentials),
    })
      .then((data) => data.json())
      .then((data) => this.setState({ allusers: data }))
      .catch((err) => console.error(err));
  }

  inputChanged = (event) => {
    const cred = this.state.credentials;
    if (
      event.target.name === "user_panchayath" ||
      "user_username" ||
      "user_wanumber" ||
      "user_district"
    ) {
      cred.person[event.target.name] = event.target.value;
    }

    if (event.target.name === "email" || "password" || "mobile_no") {
      cred[event.target.name] = event.target.value;
    }

    if (event.target.name === "confirm_password") {
      this.state.confirm_password = event.target.value;
    }
    this.setState({ credentials: cred });
  };

  render() {
    let setMailValid = false;
    let setPassValid = false;
    let data = this.state.allusers;
    if (
      data.find(
        (data) =>
          data.person?.user_username ===
          this.state.credentials.person?.user_username
      )
    ) {
      setMailValid = true;
    } else {
      setMailValid = false;
    }
    let invalid = <p className="register-invalid-id">&nbsp;</p>;
    if (this.state.confirm_password !== this.state.credentials.password) {
      setPassValid = true;
      invalid = (
        <p className="register-invalid-id">**Password does not match</p>
      );
    }
    if (this.state.registerSucess) {
      invalid = <p className="register-valid-id">**Registered successfully</p>;
    }

    //console.log(this.state.allusers.find((data) => data.id === 1));

    if (setMailValid) {
      invalid = <p className="register-invalid-id">**Username already exist</p>;
    }

    if (this.state.registerSucess) {
      setTimeout(() => {
        return <Redirect to="/" />;
      }, 2000);
    }

    return (
      <div className="container">
        <div className="imgcontainer">
          <img className="imgOne" src={ImageOne}></img>
        </div>
        <div className="register-container">
          {this.state.registerSucess ? (
            <div className="resgiter-success">
              <img className="imageTwo" src={ImageTwo}></img>
              <h2>Registered successfully</h2>
              <br></br>
              <Link to="/" className="resgiter-success-login">
                {" "}
                Login
              </Link>
            </div>
          ) : (
            <div>
              <div>
                <h1>Register</h1>
                <h6>Register to IMIT messenger</h6>
                {invalid}
              </div>
              <div className="register-form">
                <input
                  className={
                    setMailValid
                      ? "register-form-input-invalid"
                      : "register-form-input"
                  }
                  id="username-input"
                  type="text"
                  placeholder="User Name"
                  name="user_username"
                  value={this.state.credentials?.person.user_username}
                  onChange={this.inputChanged}
                  required
                ></input>
                <br></br>
                <input
                  className="register-form-input"
                  id="user_email-input"
                  type="email"
                  placeholder="Email address"
                  name="email"
                  value={this.state.credentials?.email}
                  onChange={this.inputChanged}
                  required
                ></input>
                <br></br>
                <input
                  className="register-form-input"
                  id="user_phnumber-input"
                  type="tel"
                  placeholder="Phone Number"
                  name="mobile_no"
                  value={this.state.credentials?.mobile_no}
                  onChange={this.inputChanged}
                  required
                ></input>
                <br></br>
                <input
                  className="register-form-input"
                  id="user_wanumber-input"
                  type="tel"
                  placeholder="Whatsapp Number"
                  name="user_wanumber"
                  value={this.state.credentials?.person.user_wanumber}
                  onChange={this.inputChanged}
                  required
                ></input>
                <br></br>
                <select
                  className="register-form-select"
                  name="user_panchayath"
                  value={this.state.credentials?.person.user_panchayath}
                  onChange={this.inputChanged}
                >
                  <option value="" disabled>
                    Select Panchayath
                  </option>
                  <option value="panch1">Panchayath 1</option>
                  <option value="panch2">Panchayath 2</option>
                  <option value="panch3">Panchayath 3</option>
                  <option value="panch4">Panchayath 4</option>
                </select>
                <select
                  className="register-form-select"
                  name="user_district"
                  value={this.state.credentials?.person.user_district}
                  onChange={this.inputChanged}
                >
                  <option value="" disabled>
                    Select District
                  </option>
                  <option value="district1">District 1</option>
                  <option value="district2">District 2</option>
                  <option value="district3">District 3</option>
                  <option value="district4">District 4</option>
                </select>
                <input
                  className={
                    setPassValid
                      ? "register-form-input-invalid"
                      : "register-form-input"
                  }
                  type="password"
                  placeholder="Password"
                  id="pwd"
                  name="password"
                  value={this.state.credentials.password}
                  onChange={this.inputChanged}
                  required
                ></input>
                <input
                  className={
                    setPassValid
                      ? "register-form-input-invalid"
                      : "register-form-input"
                  }
                  type="password"
                  placeholder="Confirm Password"
                  id="cnfm_pwd"
                  name="confirm_password"
                  value={this.state.confirm_password}
                  onChange={this.inputChanged}
                  required
                ></input>
                <br></br>
                <input
                  className="form-checkbox"
                  type="checkbox"
                  id="rememberpwd"
                ></input>
                <label className="form-checkbox-label" htmlFor="rememberpwd">
                  Remember password
                </label>
                <button className="btn-register" onClick={this.register}>
                  REGISTER{" "}
                </button>

                <Link to="/" className="link-register">
                  Already registered? Login
                </Link>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Register;
